@import '../../../../theme/colors.scss';

.discussion_card_container {
  -webkit-box-shadow: 0px 0px 15px 1px rgba(216, 216, 216, 0.65);
  box-shadow: 0px 0px 15px 1px rgba(216, 216, 216, 0.65);
  margin-bottom: 33px;
  max-width: 879px;
  border-radius: 20px;
  overflow: hidden;

  @media screen and (max-width: 600px) {
    margin: 0px 15px 30px 15px;
  }
}

.mobile_link {
  @media screen and (min-width: 600px) {
    display: none;
  }
}

.discussion_link {
  background-color: color(black);
  color: color(white);
  width: 268px;
  display: flex;
  align-self: center;
  justify-content: center;
  padding: 14px;
  font-size: 16px;
  border: none;
  border-radius: 24px;
  font-family: 'SF Pro Text';
  margin-bottom: 11px;
  cursor: pointer;
  text-decoration: none;

  @media screen and (max-width: 600px) {
    width: unset;
    margin: 0px 18px 24px 18px;
  }
}

.main_content {
  display: flex;
  flex-direction: row;

  @media screen and (max-width: 600px) {
    flex-direction: column;
  }

  .counter_info__mobile {
    text-align: left;
    margin-top: 14px;
    margin-left: 21px;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 600px) {
      margin: 14px 4px 0px 4px;
    }

    @media screen and (min-width: 600px) {
      display: none;
    }
  }

  .counter_info__desktop {
    @extend .counter_info__mobile;

    @media screen and (max-width: 600px) {
      display: none;
    }

    @media screen and (min-width: 600px) {
      display: flex;
    }
  }

  .desktop_link {
    display: flex;
    justify-content: center;

    @media screen and (max-width: 600px) {
      display: none;
    }
  }

  .opinion_count {
    color: color(green);
  }

  .responses_row {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 13px;
  }

  .responses_count {
    color: color(lightGrey);
  }
}

.general_info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 321px;

  @media screen and (max-width: 600px) {
    width: 100%;
  }

  .discussion_picture {
    height: 142px;
    width: 321px;
    object-fit: cover;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    @media screen and (max-width: 600px) {
      width: 100%;
    }
  }

  span {
    font-size: 16px;
    margin-bottom: 4px;
    font-family: 'SF Pro Text';
  }
}

.discussion_info {
  font-family: 'SF Pro Text Regular';
  padding: 16px 18px;
  width: 100%;

  @media screen and (max-width: 600px) {
    width: unset;
  }

  h3 {
    font-size: 20px;
    padding: 0;
    margin: 0;
  }

  p {
    font-weight: 200;
  }

  .tags_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .unsplashAuthor {
    margin-top: 12px;
    color: color(lightGrey);

    a {
      color: color(lightGrey);
    }
  }

  .user_info_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 10px;

    .avatar {
      height: 40px;
      width: 40px;
      object-fit: cover;
      border-radius: 20px;
      background-color: color(superLightGrey);
    }

    .user_name {
      margin-left: 16px;
    }
  }
}
